.banner {
  @include banner-default-heights;
  background-color: $dark-blue;

  &.with-image {
    @include banner-image-heights;

    h1,
    p {
      text-shadow: 0 0 30px rgba(0, 0, 0, .5);
    }
  }

  h1,
  p {
    margin: 0;
  }

  h1 {
    @include heading-small;
    @include body-bold;
    color: $lime-green;
    margin-bottom: .25rem;
  }

  p {
    @include body-large;
    color: $white;

    &:first-of-type {
      @include heading-xlarge;
      @include body-bold;
      line-height: 1;
    }
  }

  .cell {
    vertical-align: middle;
  }

  .container {
    width: 90%;
  }

  .details {
    @include tranzish(opacity 300ms ease-out);
  }
}

.post {
  .content {
    h1 {
      color: $dark-gray;
      margin-bottom: 0;
    }
  }

  .subtitle {
    @include body-large;
    margin-top: 0;
  }
}
