.header-default-search-mobile {
  border: solid 2px $white;
  height: 44px;
  margin: 0 auto;
  position: relative;
  width: 80%;

  button {
    background: transparent !important;
    height: 44px;
    margin: 0;
    outline: none;
    padding: 0;
    width: 30px;
  }

  &::before,
  &::after {
    @include pseudo-element;
    height: 0;
    right: -18px;
    top: 0;
    width: 0;
  }

  &::before {
    border-bottom: 22px solid transparent;
    border-left-color: $white;
    border-left-style: solid;
    border-left-width: 20px;
    border-top: 22px solid transparent;
    height: 0;
    right: -22px;
    top: -2px;
    width: 0;
  }

  &::after {
    border-bottom: 20px solid transparent;
    border-left-color: $gray-placeholder-light;
    border-left-style: solid;
    border-left-width: 18px;
    border-top: 20px solid transparent;
    right: -18px;
    top: 0;
  }

  .searchbox-submit {
    position: absolute;
    right: 0;
    top: -2px;
  }

  .searchbox-input {
    background: transparent;
    border: 0;
    color: $white;
    &::-webkit-input-placeholder { color: $white; }
    &:-moz-placeholder { color: $white; }
    &::-moz-placeholder { color: $white; }
    &:-ms-input-placeholder { color: $white; }

    &:focus {
      background: transparent;
    }
  }
}

.header-default-search {
  display: none;

  .searchbox-icon,
  .searchbox-submit {
    height: 37px;
    width: 37px;
  }

  .searchbox-input,
  .searchbox-submit {
    display: none;
  }

  form {
    .searchbox {
      .searchbox-icon,
      .searchbox-submit {
        background: transparent;
        cursor: pointer;

        i {
          color: $gradient-gray-light;
        }
      }

      .searchbox-input {
        margin: 0;
        max-width: 310px;
        position: absolute;
        right: 14px;
        top: 11px;

        &::-webkit-input-placeholder {
          color: $gradient-gray-light;
        }

        &:-moz-placeholder {
          color: $gradient-gray-light;
        }

        &::-moz-placeholder {
          color: $gradient-gray-light;
        }

        &:-ms-input-placeholder {
          color: $gradient-gray-light;
        }
      }

      .searchbox-submit {
        background: $gradient-gray-light;

        i {
          color: $white;
        }
      }
    }
  }

  &.open {
    bottom: 0;
    width: 310px;

    .searchbox,
    .searchbox-input {
      @include box-shadow(0 0 3px 1px transparentize(black, .72));
    }

    .searchbox-icon {
      position: absolute;
      right: 24px;
      top: 18px;

      i {
        color: $gradient-gray-light !important;

        &::before {
          content: '\f00d';
        }
      }
    }

    .searchbox-input {
      display: block;
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .header-default-search {
    display: inline-block;
    float: right;
    padding: 0 0 0 8px;
  }
}
