// normal font definition mixins
$font-heading-xlarge: 4.6875rem;
$font-heading-large: 3rem;
$font-heading-medium: 2.2rem;
$font-heading-small: 1.75rem;
$font-heading-xsmall: 1.25rem;

// utility mixins

// work sans regular, used for excerpt paragraphs
@mixin sans-serif {
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;
}

// poppins has regular, semibold, bold
@mixin sans-serif-secondary {
  font-family: 'Poppins Regular', sans-serif;
}

@mixin sans-serif-secondary-semibold {
  font-family: 'Poppins Semibold', sans-serif;
}

@mixin sans-serif-secondary-bold {
  font-family: 'Poppins Bold', sans-serif;
}

// fontawesome
@mixin fontawesome {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  transform: translate(0, 0);
}

// typography mixins
@mixin upper($val: .05em) {
  letter-spacing: $val;
  text-transform: uppercase;
}

@mixin un-upper {
  letter-spacing: normal;
  text-transform: none;
}

// headlines mixins, big to small
@mixin heading-generic {
  @include sans-serif-secondary-semibold;
  @include upper;
  line-height: 1.05;
}

@mixin heading-generic-bold {
  @include sans-serif-secondary-bold;
  @include upper;
  line-height: 1.05;
}

@mixin heading-xlarge {
  @include heading-generic;
  font-size: $font-heading-small;

  @media only screen and (min-width: $medium) {
    font-size: $font-heading-medium;
  }

  @media only screen and (min-width: $mediumlarge) {
    font-size: $font-heading-large;
  }

  @media only screen and (min-width: $large) {
    font-size: $font-heading-xlarge;
  }
}

@mixin heading-large {
  @include heading-generic;
  font-size: $font-heading-small;

  @media only screen and (min-width: $medium) {
    font-size: $font-heading-medium;
  }

  @media only screen and (min-width: $mediumlarge) {
    font-size: $font-heading-large;
  }
}

@mixin heading-medium {
  @include heading-generic;
  font-size: $font-heading-small;

  @media only screen and (min-width: $medium) {
    font-size: $font-heading-medium;
  }
}

@mixin heading-small {
  @include heading-generic;
  font-size: $font-heading-xsmall;

  @media only screen and (min-width: $medium) {
    font-size: $font-heading-small;
  }
}

@mixin heading-xsmall {
  @include heading-generic;
  font-size: $font-heading-xsmall;
  line-height: 1.2;
}

@mixin heading-xsmall-normal {
  @include heading-xsmall;
  font-size: 1rem;
}

@mixin heading-xsmall-bold {
  @include heading-generic-bold;
  font-size: 1rem;
  line-height: 1.2;
}

// text mixins, big to small
@mixin body-large {
  @include sans-serif-secondary;
  font-size: 1.25rem;
  line-height: 1.3;

  @media only screen and (min-width: $mediumlarge) {
    font-size: 1.6rem;
  }

  @media only screen and (min-width: $xlarge) {
    font-size: 1.8rem;
  }
}

@mixin body-medium {
  @include sans-serif-secondary;
}

@mixin body-bold {
  @include sans-serif-secondary-bold;
}

@mixin body-semi-bold {
  @include sans-serif-secondary-semibold;
}

