.members-details-list {
  .row {
    &.wide {
      max-width: 87.5rem;
    }
  }

  h3 {
    font-size: 1rem;
    margin-bottom: .5rem;
    margin-top: .35rem;
  }

  .item-titles,
  .item-details {
    display: table-cell;
    vertical-align: top;
  }

  .item-titles {
    padding-right: 20px;
    width: 65%;

    p {
      margin: 0;
    }

    ul {
      font-size: 15px;
      list-style-type: disc;
      padding-left: 20px;

      a {
        color: $gradient-gray-light;

        &:link,
        &:visited {
          color: $gradient-gray-light;
        }

        &:hover,
        &:active {
          color: $ucar-primary-light;
        }
      }
    }
  }

  .item-details {
    border-left: solid 2px;
    border-left-color: $gray-placeholder-light;
    padding-left: 20px;
    width: 35%;

    p {
      font-size: 14px;
      margin: 0;
    }
  }

  dl {
    &.date {
      margin-bottom: .15rem;

      dd,
      dt {
        display: inline-block;
      }
    }
  }

  dd,
  dl,
  dt {
    margin: 0;
    padding: 0;
  }

  dt {
    @include heading-xsmall;
    font-size: 11px;
    margin-bottom: .25rem;
  }
}

@media only screen and (min-width: $mediumlarge) {
  .members-details-list {
    dt {
      font-size: 13px;
    }
  }
}
