body {
  @include body-medium;
  font-size: 1rem;
}

header {
  .menu {
    a {
      @include upper;
      @include body-bold;
    }
  }
}

ul,
ol {
  ul {
    list-style-type: circle;
  }

  ul,
  ol {
    margin-top: 0;
    padding-left: 10px;
  }
}

strong,
b {
  @include body-semi-bold;
  letter-spacing: .05em;
}

blockquote {
  @include body-large;
  border: 0;
  color: $dark-gray;

  p {
    @include body-large;
    color: $dark-gray;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  margin-bottom: 1.5rem;
  margin-top: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong,
b {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

h1 {
  @include heading-xlarge;
}

h2 {
  @include heading-large;
}

h3 {
  @include heading-medium;
}

h4 {
  @include heading-small;
}

h5,
h6 {
  @include heading-xsmall;
}

// typography utility classes
.heading-xlarge {
  @include heading-xlarge;
}

.heading-large {
  @include heading-large;
}

.heading-medium {
  @include heading-medium;
}

.heading-small {
  @include heading-small;
}

.heading-xsmall {
  @include heading-xsmall;
}

.heading-xsmall-normal {
  @include heading-xsmall-normal;
}

.heading-xsmall-bold {
  @include heading-xsmall-bold;
}

.panel-title {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @include heading-medium;
    @include sans-serif-secondary-bold;
  }

  p {
    @include body-large;
  }
}

.panel {
  &.primary-content,
  &.page-content {
    article {
      blockquote {
        @include body-large;

        p {
          @include body-large;
        }
      }
    }
  }
}

.excerpt {
  @include sans-serif;

  p {
    @include sans-serif;
  }
}

.date {
  @include sans-serif-secondary;
  @include upper;
  font-size: 14px;
}

.utility-menu {
  li {
    @include body-semi-bold;
  }

  a {
    &.active-archive {
      @include body-semi-bold;
    }
  }
}

@media only screen and (max-width: $medium) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ul,
  ol {
    margin-bottom: .75rem;
  }

  .panel {
    &.primary-content,
    &.page-content {
      h1,
      h2,
      h3,
      h4,
      h5,
      h6,
      p,
      ul,
      ol {
        margin-bottom: .06rem;
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  header {
    ul {
      &.menu > li {
        > a {
          font-size: 12px;
          padding: 5px 7px;
        }
      }
    }
  }

  .utlity-menu {
    li {
      font-size: 11px;
    }

    a {
      &.active-archive {
        font-size: 11px;
      }
    }
  }
}

@media only screen and (min-width: $large) {
  header {
    ul {
      &.menu > li {
        > a {
          font-size: 14px;
          padding: 6px 11px;
        }
      }
    }
  }

  .utlity-menu {
    li {
      font-size: 12px;
    }

    a {
      &.active-archive {
        font-size: 12px;
      }
    }
  }
}

@media only screen and (min-width: $xlarge) {
  header {
    ul {
      &.menu > li {
        > a {
          font-size: 1rem;
          padding: 12px;
        }
      }
    }
  }

  .utlity-menu {
    li {
      font-size: 14px;
    }

    a {
      &.active-archive {
        @include body-medium;
        font-size: 14px;
      }
    }
  }

  .panel {
    &.primary-content,
    &.page-content {
      article {
        p,
        ul,
        ol {
          font-size: 1.2rem;
        }
      }
    }
  }
}
