.atmosnews {
  header {
    .submenu {
      background: transparentize($light-blue-tinted, .08);

      li {
        &:first-child {
          a {
            background-color: $light-blue-tinted;

            &::after {
              border-left-color: $light-blue-tinted;
            }
          }
        }
      }
    }
  }

  .is-stuck {
    .acronym-wrapper {
      width: 74px;
    }
  }

  .logo-wrapper {
    .acronym-definition {
      h1 {
        font-size: 1.2rem;
      }
    }
  }

  .mobile-logo {
    .acronym {
      padding-bottom: 0;
      padding-top: 2px;
      width: 32px;

      &::after {
        display: none;
      }
    }

    .description {
      font-size: 1.2rem;
      padding-bottom: 5px;
      padding-top: 5px;
    }
  }

  .title-bar {
    @include gradient-light-blue-medium-vertical;
  }

  .top-bar {
    .accordion-menu {
      background: $light-blue;
    }
  }

  .utility-mobile-wrapper {
    @include gradient-light-blue-medium-horizontal;
  }
}

// DESKTOP ONLY
@media only screen and (min-width: $mediumlarge) {
  .atmosnews {
    header {
      nav {
        background: $white;
      }
    }

    .main-nav {
      > .menu {
        > li {
          &:hover {
            > a {
              color: $light-blue-tinted;
            }
          }
        }
      }
    }

    &.subdomain {
      .submenu {
        background: $light-blue-tinted;
      }
    }
  }
}
