.featured-sections {
  a {
    height: 200px;
    padding: 25px;
    width: 100%;
  }

  ul {
    @include clearfix;
    margin-top: 20px;
  }

  li {
    @include gradient-gray-dark;
    display: block;
    float: left;
    margin-bottom: 5%;
    margin-right: 5%;
    width: 30%;

    &:nth-of-type(3n) {
      margin-right: 0;
    }

    &:hover {
      background: $gray-placeholder-light;

      h4 {
        color: $white;
      }
    }

    i {
      color: $white;
      display: block;
      font-size: 4rem;
      margin-bottom: 20px;
    }

    h4 {
      color: $lime-green;
      font-size: 16px;
      margin-bottom: 0;
    }
  }
}

@media only screen and (max-width: $medium) {
  .featured-sections {
    &.general {
      a {
        height: 165px;
        padding: 20px;
      }

      li {
        margin-right: 5%;
        width: 47.5%;

        h4 {
          font-size: .65rem;
        }

        i {
          font-size: 3rem;
        }

        &:nth-of-type(2n) {
          margin-right: 0;
        }
      }
    }
  }
}
