body {
  &.subdomain {
    .subdomain-hero {
      &.dark {
        background-color: $gradient-gray-dark;
      }
    }
  }
}

.subdomain-hero {
  @include banner-default-heights;

  a {
    color: $white;
  }

  .table {
    table-layout: fixed;
    width: 100%;
  }

  .figure-logo {
    padding-left: 20px;
    padding-right: 20px;
    width: 40%;
  }

  .figure-feature {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 60%;

    * {
      color: $white;
    }
  }

  .figure-feature-text {
    color: $white;

    * {
      margin: 0;
    }
  }

  &.with-caption {
    .table {
      height: auto !important;
    }
  }
}

@media only screen and (max-width: 760px) {
  .subdomain-hero {
    .table,
    .cell {
      display: block;
      width: 100%;
    }

    .figure-feature-text {
      padding-top: 0;
    }
  }
}
