.banner-news {
  .stay-connected-container,
  .stay-connected-cta-pages {
    width: 100%;
  }

  .stay-connected-container {
    margin-bottom: 1rem;

    .table {
      display: block;
      width: 100%;
    }

    .cell {
      display: block;
      width: 100%;
    }
  }

  ul {
    &.page-cta-referrals {
      li {
        box-shadow: 0 0 5px 2px $black-opaque;
        min-height: 180px;
        width: 47.5%;

        a {
          height: 180px;
        }

        &:nth-of-type(2n) {
          margin-right: 0;
        }
      }
    }
  }
}

.mceTmpl {
  div {
    &.atmos_sidebar_blue_right,
    &.ucar_sidebar_blue_right {
      @include sans-serif;
      background-color: mix($lightest-gray, $white, 70%);
      border-left: 5px solid $medium-gray;
      font-size: 13px;
      margin: 20px;
      padding: 20px;

      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        font-size: 1rem;
      }
    }
  }

  .ucar_imgcaption,
  .atmos_imgcaption {
    width: 100%;
  }
}

.news-archive {
  @include heading-xsmall-bold;
  border-right: solid 2px $gray-placeholder-light;
  color: $gray-placeholder-light;
  display: inline-block;
  margin-right: 20px;
  padding-right: 28px;
}

.news-filters {
  margin-bottom: 2rem;

  ul {
    ul {
      -moz-border-radius-bottomleft: 8px;
      -moz-border-radius-bottomright: 8px;
      -moz-box-shadow: 3px 3px 5px 6px rgba(20, 46, 93, .2);
      -webkit-border-bottom-left-radius: 8px;
      -webkit-border-bottom-right-radius: 8px;
      -webkit-box-shadow: 3px 3px 5px 6px rgba(20, 46, 93, .2);
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      box-shadow: 1px 1px 13px 0 rgba(20, 46, 93, .2);
      max-height: 475px;
      padding: 0;
      width: 100%;
    }

    &.dropdown {
      li {
        &:hover {
          &.is-dropdown-submenu-parent {
            &.is-active {
              > a {
                &::after {
                  display: none;
                }
              }
            }
          }
        }
      }
    }

    .is-dropdown-submenu-parent {
      @include heading-xsmall;
      display: block;
      text-align: center;

      > a {
        color: $dark-blue;
      }
    }

    .is-submenu-item {
      @include sans-serif-secondary;
      border-bottom: 1px solid;
      font-size: 1rem;
      margin: .7rem 1rem;
      padding: 0 0 .7rem;
      text-align: left;
      text-transform: capitalize;
      width: auto;

      a {
        padding: 0;
      }

      &:last-child {
        border: 0;
      }
    }
  }
}

.news-section-container {
  padding-top: 40px;
  position: relative;

  .banner-news {
    margin-top: 60px;
    position: relative;
    top: 0;
  }
}

.pagination-container {
  ul {
    display: inline-block;

    li {
      @include body-semi-bold;
      @include upper;
      display: inline-block;
      margin-right: 8px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.post-list {
  &.container {
    margin-left: auto;
    margin-right: auto;
  }

  p {
    margin-bottom: .75rem;
    margin-top: .75rem;

    &.date {
      @include body-bold;
      color: $medium-gray;
      display: inline-block;
      margin: 0;
      padding-right: 10px;
      position: relative;

      &::after {
        @include pseudo-element;
        background: $dark-gray;
        height: 100%;
        right: 0;
        top: 0;
        width: 2px;
      }
    }
  }

  .table {
    border-bottom: solid 2px #7f7c81;
    margin-bottom: 20px;
    padding-bottom: 10px;
    table-layout: fixed;
    width: 100%;

    &.no-image {
      margin-left: auto;
      margin-right: auto;
      max-width: 980px;

      .cell {
        &.figure {
          display: none;
        }

        &.description {
          width: 100%;
        }
      }
    }
  }

  .cell {
    vertical-align: top;

    &.figure,
    &.description {
      display: block;
      width: 100%;
    }

    &.description {
      h3 {
        @include heading-xsmall;
        margin-bottom: .75rem;
      }
    }
  }

  .article-details {
    @include upper;
    font-size: 14px;

    ul {
      display: inline-block;
      margin: 0;
      padding: 0;

      li {
        display: inline-block;

        &:last-child,
        &:only-child {
          .comma {
            display: none;
          }
        }
      }
    }
  }
}

.stay-connected-container {
  padding: 20px;
  text-align: center;

  *,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $white;
  }

  .table {
    table-layout: fixed;
    width: 100%;
  }

  .newsletter-signup {
    margin: 0 auto;
    width: 75%;

    form {
      margin: 0;

      &::after {
        border-left-color: $dark-blue;
      }
    }

    input {
      &[type='email'] {
        color: $dark-blue;

        &::-webkit-input-placeholder {
          color: $dark-blue;
        }

        &:-moz-placeholder {
          color: $dark-blue;
        }

        &::-moz-placeholder {
          color: $dark-blue;
        }

        &:-ms-input-placeholder {
          color: $dark-blue;
        }
      }

      &.submit {
        background: $dark-blue;
      }
    }
  }

  ul {
    &.social-menu {
      li {
        font-size: 1.8rem;

        a {
          &:link,
          &:visited {
            color: $white;
          }

          &:hover {
            i {
              color: $dark-blue;
            }
          }
        }
      }
    }
  }
}

@media only screen and (min-width: $medium) {
  .post-list {
    .table {
      border: 0;
      padding-bottom: 0;

      &.smaller-image {
        .figure {
          width: 40%;
        }

        .description {
          width: 60%;
        }
      }

      .figure,
      .description {
        display: table-cell;
      }

      .figure {
        width: 60%;
      }

      .description {
        padding-left: 40px;
        width: 40%;
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .banner-news {
    .container {
      @include clearfix;
    }

    .stay-connected-container {
      box-shadow: 0 0 5px 2px $black-opaque;
      float: left;
      min-height: 180px;
      width: 57.5%;

      .cell {
        display: table-cell;

        &.first {
          width: 65%;
        }
      }
    }

    .stay-connected-cta-pages {
      float: right;
      width: 40%;
    }
  }

  .news-archive {
    border-bottom: solid 2px $dark-blue;
    border-right: 0;
    display: block;
    font-size: 1.25rem;
    margin-bottom: 1.1rem;
    margin-right: 0;
    padding-bottom: 15px;
    padding-right: 0;
    text-align: center;

    .label {
      display: block;
      font-size: 13px;
    }
  }

  .news-filters {
    ul {
      ul {
        width: 220px;
      }
    }

    .is-dropdown-submenu-parent {
      text-align: left;
    }
  }

  .news-section-container {
    padding-top: 150px;

    .banner-news {
      left: 0;
      margin-top: -60px;
      position: absolute;
      top: 0;
      width: 100%;
    }
  }

  .pagination-container {
    zoom: .75;
  }

  .post-list {
    .table {
      display: block;

      &:last-child {
        border-bottom: 0;
      }
    }

    .cell {
      &.figure,
      &.description {
        display: block;
        padding-left: 0;
        width: 100%;
      }
    }
  }
}
