.home-hero {
  @include home-hero-image-heights;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  h2,
  h3 {
    text-shadow: 0 0 30px rgba(0, 0, 0, .5);
  }

  h2 {
    @include heading-small;
    @include body-bold;
    color: $lime-green;
    margin-bottom: 0;
  }

  h3 {
    @include heading-xlarge;
    @include body-bold;
    color: $white !important;
    margin-top: 0;
  }

  a {
    color: $lime-green;

    &.more {
      font-size: 2.2rem;
    }

    &:link,
    &:visited {
      color: $lime-green;
    }

    &:hover,
    &:active {
      color: $white !important;
    }
  }

  p {
    font-size: 1.1rem;
    text-align: left;
  }

  .panel {
    padding-bottom: 0;
  }

  .details {
    @include tranzish(opacity 100ms ease-in);
    padding: 60px 8%;
  }

  .description {
    padding-bottom: 60px;
  }

  .inner {
    display: none;

    * {
      color: $white;
    }

    a {
      font-weight: 600;
      text-transform: uppercase;
    }
  }

  .container {
    width: 94%;
  }

  &.open {
    .panel {
      padding-top: 80px;
    }

    h2,
    h3 {
      text-shadow: none;
    }

    .description {
      padding-bottom: 0;
    }

    .details {
      background: transparentize(lighten($gradient-gray-dark, 15%), .3);
      padding-bottom: 100px;

      &::before {
        display: block;
      }
    }

    .inner {
      display: block;
    }
  }
}

.featured-sections {
  &.home-primary-cta {
    padding-top: 0;

    li {
      box-shadow: 0 0 5px 2px rgba(0, 0, 0, .25);
    }

    ul {
      margin-top: 0;
    }
  }
}

@media only screen and (max-width: $medium) {
  .featured-sections {
    &.home-primary-cta {
      margin-top: -30px;

      a {
        height: 105px;
        padding: 10px;
      }

      h4 {
        font-size: .55rem;
      }

      li {
        i {
          font-size: 2.2rem;
        }
      }

      .row,
      .columns-centered {
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
        width: 100%;
      }
    }
  }
}

@media only screen and (min-width: $medium) {
  .featured-sections {
    &.home-primary-cta {
      margin-top: -60px;
    }
  }
}

@media only screen and (max-width: $mediumlarge) {
  .home-hero {
    &.open {
      .panel {
        padding-top: 40px;
      }
    }

    .details {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

@media only screen and (min-width: $large) {
  .main-content {
    &.caption {
      p {
        font-size: 1.5rem;
      }
    }
  }
}

@media only screen and (min-width: $xlarge) {
  .main-content {
    &.caption {
      p {
        font-size: 1.8rem;
      }
    }
  }
}
