.mobile-logo {
  background-color: $white;
  padding: 6px 10px;
  width: calc(100% - 55px);

  .cell {
    vertical-align: middle;
  }

  .acronym {
    border-right: solid 2px $dark-blue;
    padding: 12px 5px 12px 0;
    position: relative;
    width: 60px;

    &::after {
      @include pseudo-element;
      border-bottom: 9px solid transparent;
      border-left: 12px solid $dark-blue;
      border-top: 9px solid transparent;
      height: 0;
      right: -12px;
      top: 12px;
      width: 0;
    }

    img {
      width: 100%;
    }
  }

  .description {
    @include un-upper;
    color: $dark-blue;
    font-size: .7rem;
    line-height: 1.6;
    padding-left: 15px;
  }
}

.title-bar {
  .mobile-logo {
    .description {
      width: 200px;
    }
  }
}

.top-bar {
  .top-bar-title {
    display: none;
  }
}

.logo-wrapper {
  h1,
  p {
    margin: 0;
    padding: 0;
  }

  a {
    color: $dark-blue;
    display: block;
    outline: none;

    &:link,
    &:visited {
      color: $dark-blue;
    }

    &:hover,
    &:active {
      color: $dark-blue;
    }
  }

  .cell {
    vertical-align: middle;
  }

  .acronym-wrapper {
    border-right: solid 3px $dark-blue;

    img {
      width: 100%;
    }

    h1,
    p {
      line-height: 1;

      a {
        padding-right: 5px;
        position: relative;

        &::after {
          @include pseudo-element;
          border-bottom: 9px solid transparent;
          border-left: 12px solid $dark-blue;
          border-top: 9px solid transparent;
          display: none;
          height: 0;
          right: -12px;
          top: 0;
          width: 0;
        }
      }
    }
  }

  .acronym-definition {
    padding-left: 25px;
    padding-right: 15px;

    h1,
    p {
      @include un-upper;
      @include body-medium;
      color: $dark-blue;
    }
  }
}

.is-stuck {
  .logo-wrapper {
    .acronym-wrapper {
      h1 {
        a {
          &::after {
            top: 2px;
          }
        }
      }

      p {
        display: none;
      }
    }

    .acronym-definition {
      h1,
      p {
        line-height: 1.3;
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .top-bar {
    .top-bar-title {
      display: block;
      padding: 10px 0;
      width: 30%;
    }

    .top-bar-menus {
      -webkit-flex-wrap: wrap;
      display: flex;
      display: -webkit-flex;
      display: -ms-flexbox;
      flex-wrap: wrap;
    }

    .top-bar-left {
      -webkit-order: 2;
      display: block;
      order: 2;
      width: 100%;
    }

    .top-bar-right {
      -webkit-order: 1;
      order: 1;
      padding: 10px 0 0;
      width: 100%;
    }
  }

  .logo-wrapper {
    .acronym-wrapper {
      width: 80px;

      > :first-child {
        margin-bottom: 5px;
      }

      h1,
      p {
        a {
          &::after {
            top: 1px;
          }
        }
      }
    }

    .acronym-definition {
      h1,
      p {
        -webkit-font-smoothing: antialiased;
        font-size: .75rem;
        line-height: 1.6;
      }
    }
  }
}

@media only screen and (min-width: $large) {
  .logo-wrapper {
    .acronym-wrapper {
      width: 100px;

      h1,
      p {
        a {
          &::after {
            top: 5px;
          }
        }
      }
    }

    .acronym-definition {
      h1,
      p {
        line-height: 1.8;
      }
    }
  }
}

@media only screen and (min-width: $xlarge) {
  .is-stuck {
    .logo-wrapper {
      .acronym-wrapper {
        h1 {
          a {
            &::after {
              top: 6px;
            }
          }
        }
      }
    }
  }

  .logo-wrapper {
    .acronym-wrapper {
      width: 120px;
    }

    .acronym-definition {
      h1,
      p {
        font-size: 1rem;
      }
    }
  }
}
