.related-links {
  @include box-shadow(-5px -5px 24px -4px rgba(69, 70, 72, .56));
  background: $gray-placeholder-light;
  padding: 20px;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @include heading-xsmall;
    @include body-bold;
    color: $lime-green;
    margin-bottom: .75rem;
    margin-top: 0;
  }

  li {
    border-bottom: solid 1px $dark-gray;

    a {
      color: $white;
      display: block;
      margin-bottom: 5px;
      padding-bottom: 5px;
      padding-top: 9px;

      &:link,
      &:visited {
        color: $white;
      }

      &:hover,
      &:active {
        color: $lime-green;
      }
    }

    i {
      padding-right: 22px;
    }

    .inner {
      display: inline-block;
      max-width: 320px;

      p {
        margin: 0;
      }

      cite {
        @include body-medium;
        font-size: 14px;
        font-style: normal;
      }
    }

    &:last-child {
      border: 0;

      a {
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
}