.offerings {
  li {
    @include tranzish(all 200ms ease-in-out);
    margin-bottom: 1rem;

    .post-image {
      display: block;
      margin-bottom: 1rem;
      position: relative;

      img {
        display: block;
        width: 100%;
      }

      &::before {
        @include pseudo-element;
        @include tranzish(all 200ms ease-in-out);
        border-color: $medium-gray;
        border-style: solid;
        border-width: 14px;
        height: 100%;
        left: 0;
        opacity: 0;
        top: 0;
        width: 100%;
      }
    }

    h4 {
      @include heading-xsmall;

      a {
        color: $gray-placeholder-light;

        &:link,
        &:visited {
          color: $gray-placeholder-light;
        }

        &:hover,
        &:active {
          color: $dark-blue;
        }
      }
    }

    &:hover {
      .post-image {
        &::before {
          opacity: 1;
        }
      }
    }
  }

  .group-listing {
    .panel-title {
      h3 {
        @include heading-small;
        color: $dark-blue;
      }
    }

    .row {
      max-width: 100%;
      width: 100%;
    }
  }

  .panel-title {
    p {
      margin-bottom: 2rem;
    }
  }

  .row {
    margin-left: auto;
    margin-right: auto;
  }

  .with-gradient {
    @include gradient-white-transparent;
    padding-top: .9375rem;
  }

  &.portraits {
    li {
      @include clearfix;

      h4 {
        margin-bottom: 0;
        margin-top: 0;
      }

      .post-image {
        float: left;
        width: 30%;
      }

      .details {
        float: right;
        width: 65%;
      }
    }
  }
}

.offerings + .offerings {
  padding-bottom: 0;
  padding-top: 0;
}

@media only screen and (min-width: 641px) and (max-width: 1023px) {
  .offerings {
    li {
      @include clearfix;
      font-size: .9rem;

      .post-image {
        float: left;
        width: 45%;
      }

      .details {
        float: right;
        width: 50%;
      }
    }

    &.portraits {
      .post-image {
        float: left;
        width: 30%;
      }

      .details {
        float: right;
        width: 65%;
      }
    }
  }
}

@media only screen and (min-width: $large) {
  .offerings {
    li {
      font-size: 1rem;
      min-height: 550px;
    }

    &.portraits {
      li {
        min-height: 290px;
      }
    }
  }
}
