ul {
  &.page-cta-referrals {
    @include clearfix;

    > li {
      @include gradient-gray-dark;
      display: inline-block;
      float: left;
      margin-right: 5%;
      text-align: center;

      a {
        display: table;
        padding: 20px;
        width: 100%;
      }

      i {
        color: $white;
        font-size: 4rem;
      }

      h4 {
        color: $lime-green;
        font-size: 16px;
        margin-bottom: 0;
      }

      &:hover {
        background: $gray-placeholder-light;

        h4 {
          color: $white;
        }
      }
    }
  }

  &.social-menu {
    font-size: 1.2rem;
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      font-size: 1.5rem;
      margin-right: 5px;

      a {
        &:link,
        &:visited {
          color: $white;
        }

        &:hover {
          i {
            color: $lime-green;
          }
        }
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.panel {
  &.organizational-sitemap {
    .row {
      background: $white;
      max-width: 70rem;
      padding: 20px;
    }

    abbr {
      border: 0;
      color: $light-gray !important;
      cursor: pointer;
      min-width: 50px;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      color: $gradient-gray-dark;
    }

    h3,
    h4,
    h5,
    h6 {
      font-size: 1.5rem !important;
    }

    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;

      &.org-list {
        margin-bottom: 2rem;

        li {
          a {
            display: table;
            table-layout: fixed;
            text-decoration: none !important;
            width: 100%;

            abbr,
            span {
              display: table-cell;
              vertical-align: middle;
            }

            abbr {
              width: 16%;
            }
          }
        }

        > li {
          @include body-semi-bold;
          border-bottom: solid 1px $gradient-gray-light;
          margin-bottom: 8px;
          padding-bottom: 8px;

          &:last-child {
            border-bottom: 0;
          }

          > ul {
            margin-top: 5px;

            > li {
              @include body-medium;
              border-bottom: 0;
              font-size: .95rem;
              margin-bottom: 4px;
              padding-bottom: 0;

              > a {
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }

    a {
      color: $gray-placeholder-light !important;

      &:link,
      &:visited {
        color: $gray-placeholder-light !important;
      }

      &:hover,
      &:active {
        color: $gray-placeholder-dark !important;
      }
    }
  }
}

@media only screen and (max-width: $medium) {
  ul {
    &.page-cta-referrals > li {
      i {
        font-size: 3rem;
      }

      h4 {
        font-size: .65rem;
      }
    }
  }

  .panel {
    &.organizational-sitemap {
      ul {
        &.org-list {
          li {
            a {
              abbr {
                width: 30%;
              }
            }
          }
        }
      }
    }
  }
}
