.ucar {
  header {
    .submenu {
      background: transparentize($ucar-primary-dark, .08);

      li {
        &:first-child {
          a {
            background-color: $ucar-primary-dark;

            &::after {
              border-left-color: $ucar-primary-dark;
            }
          }
        }
      }
    }
  }

  .logo-wrapper {
    .acronym-wrapper {
      h1 {
        a {
          color: $dark-blue;

          &:link,
          &:visited,
          &:hover,
          &:active {
            color: $dark-blue;
          }

          &::after {
            display: block;
          }
        }
      }
    }
  }

  .title-bar {
    @include gradient-green-medium-vertical;
  }

  .top-bar {
    .accordion-menu {
      background: $ucar-primary-light;
    }
  }

  .utility-mobile-wrapper {
    @include gradient-green-medium-horizontal;
  }
}

@media only screen and (min-width: $mediumlarge) {
  .ucar {
    header {
      nav {
        background: $white;
      }
    }

    .main-nav {
      > .menu {
        > li {
          &:hover {
            > a {
              color: $ucar-primary-light;
          }
        }
      }
      }
    }
  }
}
