.ucar {
  background-image: url('../img/bg-body-ucar-v4.png');
  background-position: top right;
  background-repeat: repeat-y;

  a {
    color: $ucar-primary-dark;

    &:hover,
    &:active {
      color: $ucar-primary-light;
    }
  }

  h3,
  h4,
  h5,
  h6 {
    color: $ucar-primary-light;
  }

  button {
    background: none;
  }

  footer {
    .newsletter-signup {
      form {
        &::after {
          border-left-color: $ucar-primary-light;
        }

        input {
          &.submit {
            background: $ucar-primary-light;
          }
        }
      }
    }
  }

  ol {
    &.post-list {
      p {
        &.date {
          color: $ucar-primary-light;
        }
      }
    }
  }

  ul {
    &.page-cta-referrals {
      li {
        &:hover {
          @include gradient-green-medium;
        }
      }
    }
  }

  .banner {
    background-color: $ucar-primary-light;
  }

  .component {
    &.related-links {
      @include gradient-green-medium;
    }

    &.sidebar-breadcrumb {
      li {
        a {
          &::before {
            border-right-color: $ucar-primary-dark;
          }
        }

        &.active {
          > a {
            background: $ucar-primary-dark;
            color: $white;
          }
        }
      }

      .home {
        &::before {
          border-right-color: $ucar-primary-dark;
        }
      }
    }
  }

  .cta-wrapper {
    &.white-on-color {
      background: $ucar-primary-light;

      &::after {
        border-left-color: $ucar-primary-light;
      }

      a {
        &:link,
        &:visited {
          color: $white;
        }

        &:hover,
        &:active {
          color: $lime-green;
        }
      }
    }

    &.dark-blue-on-gray {
      &:hover {
        border-color: $ucar-primary-light;

        &::before {
          border-left-color: $ucar-primary-light;
        }
      }

      &.back-button {
        &:hover {
          &::before {
            border-right-color: $ucar-primary-light;
          }
        }
      }
    }
  }

  .header-default-search-mobile {
    &::after {
      border-left-color: $ucar-primary-dark;
    }
  }

  .home-hero {
    &.open {
      .details {
        background: transparentize($ucar-primary-light, .3);
      }
    }
  }

  .news-archive {
    color: $ucar-primary-dark;
  }

  .panel {
    &.related-posts {
      .featured-post,
      li {
        &:hover {
          border-color: $ucar-primary-dark;

          h4 {
            background: $ucar-primary-dark;
          }
        }
      }
    }

    &.members-details-list {
      .item-details {
        border-left-color: $ucar-primary-light;
      }
    }

    &.featured-sections {
      li {
        &:hover {
          @include gradient-green-medium;
        }
      }
    }

    &.home-happenings {
      .component {
        &.related-links {
          background: transparent;
        }
      }
    }

    &.offerings {
      li {
        h4 {
          a {
            color: $ucar-primary-dark;

            &:link,
            &:visited {
              color: $ucar-primary-dark;
            }

            &:hover,
            &:active {
              color: $dark-blue;
            }
          }
        }

        .post-image {
          &::before {
            border-color: $ucar-primary-light;
          }
        }
      }
    }

    &.programs-listing {
      background-color: $ucar-primary-light;
    }

    &.related-page-section {
      .figure {
        border-color: $ucar-primary-light;
      }

      .details {
        background: $ucar-primary-light;

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
          color: $dark-gray;
        }
      }
    }

    &.related-sections {
      a {
        &:hover {
          .figure {
            @include gradient-green-medium;
          }
        }
      }
    }
  }

  .subdomain-hero {
    background-color: $ucar-primary-light;
  }

  .stay-connected-container {
    background: $ucar-primary-light;

    .newsletter-signup {
      input {
        &[type='email'] {
          &::-webkit-input-placeholder {
            color: $ucar-primary-dark;
          }

          &:-moz-placeholder {
            color: $ucar-primary-dark;
          }

          &::-moz-placeholder {
            color: $ucar-primary-dark;
          }

          &:-ms-input-placeholder {
            color: $ucar-primary-dark;
          }
        }
      }
    }
  }
}
