.home-happenings {
  .table {
    table-layout: fixed;
    width: 100%;

    &.lower {
      direction: rtl;

      .cell {
        direction: ltr;
      }
    }
  }

  .featured-story,
  .publications,
  .spotlight,
  .news {
    display: block;
    height: auto;
    width: 100%;
  }

  .spotlight {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 40px;
    position: relative;
    vertical-align: middle;

    &::before {
      @include pseudo-element;
      @include tranzish(background 80ms ease-in);
      background: rgba(0, 0, 0, .15);
      height: 100%;
      left: 0;
      top: 0;
      width: 100%;
    }

    &:hover {
      &::before {
        background: rgba(0, 0, 0, .05);
      }
    }

    h4 {
      @include heading-xsmall;
      color: $lime-green;
      margin-bottom: 0;
    }

    h5 {
      @include heading-large;
      color: $white;
    }

    h4,
    h5 {
      @include body-bold;
      text-shadow: 0 0 30px rgba(0, 0, 0, .5);
    }
  }

  .panel {
    &.related-page-section {
      padding-bottom: 0;

      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        color: $dark-gray;
      }

      .details {
        vertical-align: top;
      }
    }
  }

  .component {
    &.related-links {
      ul {
        margin-bottom: 1.5rem;
      }
    }
  }

  .publications,
  .news {
    @include gradient-gray-dark-linear;
    padding: 20px;
  }

  .publications {
    @include box-shadow(-5px -5px 12px -4px rgba(69, 70, 72, .56));
  }

  .news {
    @include box-shadow(5px 5px 12px -4px rgba(69, 70, 72, .56));
  }
}

@media only screen and (min-width: 760px) {
  .home-happenings {
    .featured-story,
    .spotlight {
      width: 60%;
    }

    .news,
    .publications {
      padding: 40px;
      width: 40%;
    }

    .news {
      &::after {
        display: none;
      }
    }

    .figure {
      height: 180px;
    }

    .panel {
      &.related-page-section {
        .figure,
        .details {
          width: 50%;
        }
      }
    }

    .table {
      .cell {
        display: table-cell;
      }
    }
  }
}

@media only screen and (min-width: $large) {
  .home-happenings {
    .featured-story,
    .spotlight {
      width: 68%;
    }

    .publications,
    .news {
      width: 32%;
    }
  }
}
