.atmosnews {
  a {
    color: $light-blue-tinted;

    &:hover,
    &:active {
      color: $light-blue;
    }
  }

  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $light-blue-tinted;
  }

  button {
    background: none;
  }

  footer {
    .newsletter-signup {
      form {
        &::after {
          border-left-color: $light-blue;
        }

        input {
          &.submit {
            background: $light-blue;
          }
        }
      }
    }
  }

  ul {
    &.page-cta-referrals {
      li {
        &:hover {
          background: $light-blue-tinted-darker;
        }
      }
    }
  }

  ol {
    &.post-list {
      p {
        &.date {
          color: $light-blue-tinted-darker;
        }
      }
    }
  }

  .cta-wrapper {
    &.white-on-color {
      background: $light-blue-tinted-darker;

      &::after {
        border-left-color: $light-blue-tinted-darker;
      }

      a {
        &:link,
        &:visited {
          color: $white;
        }

        &:hover,
        &:active {
          color: $lime-green;
        }
      }
    }

    &.dark-blue-on-gray {
      &:hover {
        border-color: $light-blue-tinted-darker;

        &::before {
          border-left-color: $light-blue-tinted-darker;
        }
      }

      &.back-button {
        &:hover {
          &::before {
            border-right-color: $light-blue-tinted-darker;
          }
        }
      }
    }
  }

  .component {
    &.related-links {
      background: $light-blue-tinted-darker;
    }
  }

  .header-default-search-mobile {
    &::after {
      border-left-color: $light-blue-tinted;
    }
  }

  .news-archive {
    color: $light-blue-tinted-darker;
  }

  .panel {
    &.programs-listing {
      background-color: $light-blue-tinted-darker;
    }
  }

  .stay-connected-container {
    background: $light-blue-tinted-darker;

    .newsletter-signup {
      input {
        &[type='email'] {
          &::-webkit-input-placeholder {
            color: $light-blue-tinted-darker;
          }

          &:-moz-placeholder {
            color: $light-blue-tinted-darker;

          }

          &::-moz-placeholder {
            color: $light-blue-tinted-darker;
          }

          &:-ms-input-placeholder {
            color: $light-blue-tinted-darker;
          }
        }
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .atmosnews {
    footer {
      .footer-menu-tertiary {
        margin-top: 0;
      }
    }
  }
}
