// cool colors
$dark-blue: #142e5d;
$blue-gray: #eef1f6;

// warm colors
$lime-green: #bfd600;

// neutral colors
$white: #fff;
$lightest-gray: #dee5f0;
$light-gray: #7f7c81;
$gray: #58565a;
$medium-gray: #c8d4e7;
$dark-gray: #262526;
$darkest-gray: #414143;
$logo-inactive-gray: #abadaf;
$black: #000;

// colors with opacity
$white-opaque: rgba(255, 255, 255, .5);
$black-opaque: rgba(0, 0, 0, .25);

// gradient grays
$gradient-gray-light: #454648;
$gradient-gray-dark: #323133;

// for the default theme
$gray-placeholder-light: lighten($gradient-gray-dark, 30%);
$gray-placeholder-dark: lighten($gradient-gray-dark, 15%);

// body.ucar
$ucar-primary-light: #1d8995;
$ucar-primary-dark: #187382;
$ucar-primary-lightest: #00c4a5;

// body.ncar
$ncar-primary-light: #19799c;
$ncar-primary-dark: #15668a;
$ncar-primary-lightest: #28bed3;

// body.atmosnews
$light-blue: #5ec2ea;
$light-blue-tinted: #1a96c7;
$light-blue-tinted-darker: #0992c9;

// alerts
$red: e50000;
$yellow: #ffff32;
