.panel {
  &.related-page-section {
    h3 {
      @include body-large;
      @include un-upper;
      @include body-semi-bold;
      margin-top: 0;
    }

    p {
      font-size: .9em;
      line-height: 1.5;
    }

    .columns {
      padding: 0;
    }

    .row {
      width: 100%;
    }

    .figure {
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
      border-style: solid;
      border-width: 10px;
      display: block;
      height: 180px;
    }

    .details {
      background: $dark-gray;
      display: block;
      padding: 15px 20px;

      * {
        color: $white;
      }
    }
  }
}

@media only screen and (min-width: 550px) {
  .panel {
    &.related-page-section {
      .row {
        width: 96%;
      }

      .figure {
        height: 240px;
      }
    }
  }
}

@media only screen and (min-width: 760px) {
  .panel {
    &.related-page-section {
      p {
        font-size: 1rem;
      }

      .figure {
        border-width: 14px 0 14px 14px;
        height: 400px;
        width: 66.66%;
      }

      .details {
        width: 33.33%;
      }

      .table {
        .cell {
          display: table-cell;
        }
      }
    }
  }
}
