.panel {
  &.related-posts {
    h4 {
      @include body-medium;
      @include un-upper;
      background: $dark-gray;
      bottom: -1px;
      color: $white;
      font-size: 1rem;
      left: 0;
      letter-spacing: .06em;
      margin: 0;
      padding-top: 10px;
      position: absolute;
      width: 100%;

      i {
        font-size: 80%;
      }
    }

    a {
      color: $white;
      display: block;
      outline: none;
      position: relative;
    }

    li,
    .featured-post {
      border-style: solid;
      border-width: 10px;
      margin-bottom: 20px;

      &:hover {
        border-color: $gray-placeholder-light;

        h4 {
          background: $gray-placeholder-light;
        }
      }
    }

    .featured-post {
      img {
        width: 100%;
      }

      h4 {
        @include body-large;
        @include body-semi-bold;
        color: $white;
      }
    }

    .latest-post {
      padding-right: 4px;
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .panel {
    &.related-posts {
      .featured-post,
      li {
        border-width: 14px;
      }
    }
  }
}
