li {
  &:hover {
    &.is-dropdown-submenu-parent {
      > a {
        &::after {
          border: inset 12px;
          border-bottom-width: 0;
          border-color: $white transparent transparent;
          border-top-style: solid;
          content: '';
          display: block;
          height: 0;
          left: 40%;
          top: 114%;
          width: 0;
        }
      }
    }
  }
}

.top-bar {
  background: none;
  background-color: $lightest-gray;
  margin: 0 auto;
  max-width: 85rem;
  padding: 0;
}

.utility-menu {
  @include clearfix;
  padding: 15px 20px;
}

.utility-mobile-wrapper {
  background: $gray-placeholder-light;
  padding-top: 15px;
}

.utility-parents {
  display: table;
  font-size: 1.2rem;
  padding-top: 15px;
  table-layout: fixed;
  text-align: center;
  width: 100%;

  a {
    display: block;
    padding: 10px;

    &:link,
    &:visited {
      color: $white;
    }

    &:hover,
    &:active {
      color: $dark-gray;
    }
  }

  li {
    display: table-cell;
    padding-bottom: 10px;
  }
}

.utility-search {
  &.desktop-only {
    display: none;
  }
}

.utility-wrapper {
  background: $gradient-gray-dark;

  li {
    a {
      color: $white;
      display: block;
      padding: 5px 0;

      &:hover,
      &:active {
        color: $white;
      }
    }
  }

  ul {
    background: none;
  }
}

@media only screen and (min-width: $mediumlarge) {
  li {
    &:hover {
      &.is-dropdown-submenu-parent {
        > a {
          &::after {
            top: 120%;
            z-index: 2;
          }
        }
      }
    }
  }

  .is-stuck {
    .utility-wrapper {
      display: none;
    }
  }

  .main-nav {
    > ul {
      position: relative;
    }
  }

  .top-bar {
    background-color: $white;
    width: 98%;

    ul {
      background-color: $white;
    }
  }

  .utility-menu {
    li {
      font-size: 11px;
    }
  }

  .utility-mobile-wrapper {
    display: none;
  }

  .utility-search {
    &.desktop-only {
      display: inline-block;
      float: right;
    }
  }

  .utility-wrapper {
    flex: 0 0 100%;
    text-align: right;

    li {
      display: inline-block;

      a {
        color: $light-gray;
        padding: 0 10px;

        &:active,
        &:hover {
          color: $gray;
        }
      }
    }

    .utility-menu {
      @include upper;
      padding: 0;
    }
  }
}

@media only screen and (min-width: $large) {
  .utility-menu {
    li {
      font-size: 12px;
    }
  }
}

@media only screen and (min-width: $xlarge) {
  li {
    &:hover {
      &.is-dropdown-submenu-parent {
        > a {
          &::after {
            top: 115%;
          }
        }
      }
    }
  }

  .utility-menu {
    li {
      @include sans-serif-secondary;
      font-size: 14px;
    }
  }
}
