.programs-listing {
  background: $gray-placeholder-light;

  h4,
  p {
    color: $white;
  }

  li {
    margin-bottom: 1rem;

    h4 {
      @include body-large;
      @include un-upper;
      @include body-bold;
      letter-spacing: .02em;
      margin-bottom: .25em;
      width: calc(100% - 150px);
    }

    p {
      @include body-semi-bold;
      color: $black;
      font-size: 14px;
      letter-spacing: .05em;
      line-height: 1.7;
      margin: 0;
    }
  }

  .row {
    width: 90%;

    > div {
      &:first-child {
        padding-left: 0;
        padding-right: 1.875rem;
      }

      &:last-child {
        padding-left: 1.875rem;
        padding-right: 0;
      }
    }
  }

  .panel-title {
    margin-bottom: 2rem;

    h3 {
      color: $lime-green;
    }
  }

  .table {
    > div {
      &:last-child {
        width: 150px;

        .cta-wrapper {
          float: right;
        }
      }
    }
  }
}

@media only screen and (max-width: $medium) {
  .programs-listing {
    li {
      h4 {
        width: 100%;
      }
    }

    .row {
      width: 90%;

      > div {
        &:first-child,
        &:last-child {
          padding: 0;
        }
      }
    }
  }
}
