.component {
  &.sidebar-breadcrumb {
    @include gradient-gray-dark;
    @include box-shadow(-5px -5px 24px -4px rgba(69, 70, 72, .56));
    padding: 40px 0;

    span {
      display: inline-block;
      line-height: 1.35;
      max-width: 320px;
    }

    a {
      color: $lightest-gray;

      &:link,
      &:visited,
      &:active {
        color: $lightest-gray;
      }
    }

    ul {
      > li {
        @include upper;

        > ul > li {
          @include un-upper;

          > a {
            padding-left: 70px;

            i {
              font-size: .65rem;
              left: 45px;
              padding-top: 9px;
            }

            &:hover {
              i {
                font-size: 1.1rem;
                left: 42px;
                padding-top: 6px;
              }
            }
          }

          > ul > li {
            a {
              padding-left: 95px;

              i {
                left: 70px;
              }

              &:hover {
                i {
                  left: 66px;
                }
              }
            }
          }

          &.active {
            > ul {
              display: block;
            }

            > a > i {
              font-size: 1.1rem;
              left: 42px;
              padding-top: 6px;

              &::before {
                content: '\f10c';
              }
            }
          }
        }

        &.active {
          > a {
            color: $white;

            &::before {
              display: block;
            }
          }
        }
      }

      li {
        &.active {
          @include upper;
        }
      }

      ul {
        list-style-type: none;
        margin-left: 0;
        padding-left: 0;

        ul {
          display: none;
        }
      }
    }

    li {
      a {
        display: block;
        padding-bottom: 5px;
        padding-left: 45px;
        padding-right: 20px;
        padding-top: 4px;
        position: relative;

        &::before {
          @include pseudo-element;
          border-bottom: 16px solid transparent;
          border-right-color: $gray-placeholder-light;
          border-right-style: solid;
          border-right-width: 15px;
          border-top: 16px solid transparent;
          display: none;
          height: 0;
          left: -15px;
          top: 0;
          width: 0;
        }

        i {
          left: 20px;
          padding-top: 6px;
          position: absolute;
          top: 0;
          width: 20px;
        }

        &:hover {
          color: $lime-green;

          i {
            &::before {
              content: '\f10c';
            }
          }
        }
      }

      &.active {
        > a {
          background: $gray-placeholder-light;
          color: $white;
        }
      }
    }

    .home {
      @include upper;
      display: block;
      padding-bottom: 5px;
      padding-left: 45px;
      padding-right: 20px;
      padding-top: 4px;
      position: relative;

      &::before {
        @include pseudo-element;
        border-bottom: 16px solid transparent;
        border-right-color: $gray-placeholder-light;
        border-right-style: solid;
        border-right-width: 15px;
        border-top: 16px solid transparent;
        display: none;
        height: 0;
        left: -15px;
        top: 0;
        width: 0;
      }

      i {
        left: 20px;
        padding-top: 6px;
        position: absolute;
        top: 0;
        width: 20px;
      }

      &:hover {
        color: $lime-green;

        i {
          &::before {
            content: '\f10c';
          }
        }
      }
    }
  }
}

@media only screen and (max-width: $medium) {
  .component {
    &.sidebar-breadcrumb {
      ul {
        > li {
          &.active {
            > a {
              &::before {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

