.panel {
  &.related-sections {
    ul {
      @include clearfix;
    }

    li {
      background: $white;
      display: inline-block;
      float: left;
      margin-bottom: 4%;
      margin-right: 3.5%;
      text-align: center;
      width: 31%;

      &:nth-of-type(3n) {
        margin-right: 0;
      }

      .table {
        height: 225px;
        table-layout: fixed;
        width: 100%;
      }

      .cell {
        vertical-align: middle;
        width: 50%;
      }
    }

    i {
      color: $white;
      display: block;
      font-size: 4rem;
      margin-bottom: 20px;
    }

    h4 {
      @include heading-xsmall;
      color: $lime-green;
      font-size: 16px;
      margin-bottom: 0;
    }

    p {
      margin: 0;
    }

    a {
      &:hover {
        .figure {
          background: $gray-placeholder-light;
        }
      }
    }

    .excerpt {
      color: $dark-gray;
    }

    .figure {
      @include gradient-gray-dark;
      padding: 20px;
    }

    .figcaption {
      padding: 10px;
    }
  }
}

@media only screen and (max-width: 860px) {
  .panel {
    &.related-sections {
      ul {
        margin: 0 auto;
        width: 92%;
      }

      li {
        margin-right: 5%;
        width: 47.5%;

        h4 {
          font-size: .65rem;
        }

        i {
          font-size: 3rem;
        }

        &:nth-of-type(2n) {
          margin-left: 0;
          margin-right: 0;
        }

        .figcaption {
          display: none;
        }

        .table {
          height: 140px;
        }
      }
    }
  }
}

@media only screen and (max-width: 1095px) {
  .panel {
    &.related-sections {
      li {
        margin-right: 5%;
        width: 47.5%;

        .table {
          height: 190px;
        }

        &:nth-of-type(2n) {
          margin-right: 0;
        }
      }
    }
  }
}
