.post-stamp {
  width: 100%;

  .social-share {
    font-size: 1.3rem;
    text-align: right;

    a {
      margin-right: 4px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .date {
    @include body-semi-bold;
    font-size: 1rem;
  }
}

.component {
  &.article-details {
    @include gradient-gray-dark;
    @include box-shadow(-5px -5px 24px -4px rgba(69, 70, 72, .56));
    padding: 20px;

    * {
      color: $white;
    }

    h6 {
      font-size: 1rem;
      margin-bottom: 0;

      a {
        color: $lime-green;
      }
    }

    a {
      .term {
        color: $lime-green;
      }

      &:link,
      &:visited {
        .term {
          color: $lime-green;
        }
      }

      &:hover,
      &:active {
        .term {
          color: $lime-green;
          text-decoration: underline;
        }
      }
    }

    .term-list {
      a {
        &:only-child,
        &:last-child {
          .comma {
            display: none;
          }
        }
      }

      > span {
        &:only-child,
        &:last-child {
          .comma {
            display: none;
          }
        }
      }
    }
  }
}
