@mixin border-radius($value) {
  -moz-border-radius: $value;
  -webkit-border-radius: $value;
  border-radius: $value;
}

// good for vertical align middling
@mixin translate($value) {
  -moz-transform: $value;
  -ms-transform: $value;
  -o-transform: $value;
  -webkit-transform: $value;
  transform: $value;
}

@mixin tranzish($value) {
  -moz-transform: $value;
  -ms-transform: $value;
  -o-transform: $value;
  -webkit-transform: $value;
  transform: $value;
}

@mixin transform($value) {
  -moz-transform: $value;
  -o-transform: $value;
  -webkit-transform: $value;
}

@mixin delay($value) {
  -moz-transition-delay: $value;
  -o-transition-delay: $value;
  -webkit-transition-delay: $value;
  transition-delay: $value;
}

@mixin box-shadow($value) {
  -moz-box-shadow: $value;
  -webkit-box-shadow: $value;
  box-shadow: $value;
}

@mixin clearfix {
  &::after {
    clear: both;
    content: '';
    display: table;
  }
}

@mixin pseudo-element {
  content: '';
  display: block;
  position: absolute;
}

@mixin narrow-max-width {
  max-width: 60rem;
}

@mixin banner-image-heights {
  .table {
    height: auto;
  }

  @media only screen and (min-width: 760px) {
    .table {
      height: 300px;
    }
  }

  @media only screen and (min-width: $mediumlarge) {
    .table {
      height: 440px;
    }
  }

  @media only screen and (min-width: $xxlarge) {
    .table {
      height: 600px;
    }
  }
}

@mixin banner-default-heights {
  .table {
    height: 400px;
  }

  @media only screen and (max-width: 760px) {
    .table {
      height: auto;
    }
  }
}

@mixin home-hero-image-heights {
  .table {
    height: 800px;
  }

  @media only screen and (max-width: $xlarge) {
    .table {
      height: 650px;
    }
  }

  @media only screen and (max-width: 990px) {
    .table {
      height: 500px;
    }
  }

  @media only screen and (max-width: 760px) {
    .table {
      height: auto;
    }
  }
}

// UCAR COLOR MIXINS
@mixin gradient-green-medium {
  background: rgba(29, 137, 149, 1);
  background: radial-gradient(ellipse at center, rgba(29, 137, 149, 1) 0%, rgba(24, 116, 130, 1) 100%);
}

@mixin gradient-green-medium-vertical {
  background: rgba(29, 137, 149, 1);
  background: linear-gradient(to bottom, rgba(29, 137, 149, 1) 0%, rgba(24, 116, 130, 1) 100%);
}

@mixin gradient-green-medium-horizontal {
  background: rgba(29, 137, 149, 1);
  background: linear-gradient(to right, rgba(29, 137, 149, 1) 0%, rgba(24, 116, 130, 1) 100%);
}

// NCAR COLOR MIXINS
@mixin gradient-blue-medium {
  background: rgba(25, 121, 156, 1);
  background: radial-gradient(ellipse at center, rgba(25, 121, 156, 1) 0%, rgba(21, 103, 138, 1) 100%);
}

@mixin gradient-blue-medium-vertical {
  background: rgba(25, 121, 156, 1);
  background: linear-gradient(to bottom, rgba(25, 121, 156, 1) 0%, rgba(21, 103, 138, 1) 100%);
}

@mixin gradient-blue-medium-horizontal {
  background: rgba(25, 121, 156, 1);
  background: linear-gradient(to right, rgba(25, 121, 156, 1) 0%, rgba(21, 103, 138, 1) 100%);
}

// ATMOSNEWS COLOR MIXINS
@mixin gradient-light-blue-medium-vertical {
  background: rgba(94, 194, 234, 1);
  background: linear-gradient(to bottom, rgba(94, 194, 234, 1) 0%, rgba(26, 150, 199, 1) 100%);
}

@mixin gradient-light-blue-medium-horizontal {
  background: rgba(94, 194, 234, 1);
  background: linear-gradient(to right, rgba(94, 194, 234, 1) 0%, rgba(26, 150, 199, 1) 100%);
}

// DEFAULT COLORS SANS BODY.UCAR,  BODY.NCAR,  BODY.ATMOSNEWS
@mixin gradient-gray-dark {
  background: rgba(69, 70, 72, 1);
  background: radial-gradient(ellipse at center, rgba(69, 70, 72, 1) 0%, rgba(50, 49, 51, 1) 100%);
}

@mixin gradient-gray-dark-linear {
  background: rgba(50, 49, 51, 1);
  background: linear-gradient(to right, rgba(50, 49, 51, 1) 0%, rgba(69, 70, 72, 1) 100%);
}

// SUBTLE GLOBAL MIXINS
@mixin gradient-white-transparent {
  background: rgba(255, 255, 255, .5);
  background: linear-gradient(to bottom, rgba(255, 255, 255, .4) 0%, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0) 100%);
}
