header {
  background: $white;

  button {
    &.fa,
    .fa {
      color: $white;
    }

    &.fa {
      cursor: pointer;
      padding: 18px 20px;
    }
  }

  h1 {
    font-size: 1rem;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  .menu {
    li {
      display: block;

      a {
        padding: 8px 20px 8px 56px;

        &:hover,
        &:active {
          color: $dark-gray;
        }
      }
    }
  }
}

.is-accordion-submenu-parent {
  > a {
    &::after {
      -webkit-transform: rotate(45deg);
      border-bottom: .13em solid;
      border-left: 0;
      border-right: .13em solid;
      border-top: 0;
      font-size: 40px;
      height: .4em;
      left: 1rem;
      margin-left: .21em;
      top: .2em;
      transform: rotate(45deg);
      width: .4em;
    }
  }
}

.sticky {
  z-index: 5;
}

.is-stuck {
  -moz-box-shadow: 0 8px 6px -6px rgba(0, 0, 0, .1);
  -webkit-box-shadow: 0 8px 6px -6px rgba(0, 0, 0, .1);
  background: $white;
  box-shadow: 0 8px 6px -6px rgba(0, 0, 0, .1);
  width: 100%;
}

.main-nav {
  flex: 0 0 100%;

  ul {
    &.accordion-menu {
      padding-bottom: 7px;
      padding-top: 15px;
    }

    li {
      &.is-accordion-submenu-parent {
        &[aria-expanded='true'] {
          > a {
            color: $lime-green;

            &::after {
              -webkit-transform: rotate(225deg);
              color: $lime-green;
              top: 12px;
              transform: rotate(225deg);
            }
          }
        }
      }
    }
  }

  .menu {
    li {
      a {
        color: $white;

        &:hover {
          color: $dark-gray;
        }

        &:active {
          color: $dark-gray;
        }
      }
    }
  }

  .submenu {
    background: $gray-placeholder-dark;
    border: 0;
    padding: 25px;

    li {
      &:first-child {
        padding-bottom: 15px;

        a {
          @include heading-xsmall;
          @include tranzish(all 200ms ease-in-out);
          border-bottom: solid 2px $white;
          border-left: solid 2px $white;
          border-top: solid 2px $white;
          color: $white;
          height: 44px;
          padding-top: 9px;
          position: relative;
          text-align: center;
          width: 80%;

          &::before,
          &::after {
            @include pseudo-element;
            @include tranzish(all 200ms ease-in-out);
            border-bottom: 20px solid transparent;
            border-left-color: $gray-placeholder-light;
            border-left-style: solid;
            border-left-width: 18px;
            border-top: 20px solid transparent;
            height: 0;
            margin-top: 0;
            right: -18px;
            top: 0;
            width: 0;
          }

          &::before {
            border-bottom: 22px solid transparent;
            border-left-color: $white;
            border-left-style: solid;
            border-left-width: 20px;
            border-top: 22px solid transparent;
            height: 0;
            left: 100%;
            top: -2px;
            width: 0;
          }

          &:hover {
            color: $lime-green;
          }
        }

        p {
          display: none;
        }
      }

      &:not(:first-child) {
        a {
          @include sans-serif-secondary;
          color: $white;
          display: block;
          padding: 6px 12px 16px 28px;
          position: relative;
          text-transform: none;

          &::before,
          &::after {
            @include pseudo-element;
          }

          &::before {
            @include fontawesome;
            color: $white;
            content: '\f10c';
            font-size: 1.05rem;
            left: 6px;
            top: 5px;
          }

          &::after {
            background: $white;
            height: calc(100% - 14px);
            left: 12px;
            top: 20px;
            width: 2px;
          }

          &:hover {
            color: $lime-green;

            &::before {
              color: $lime-green;
              content: '\f140'; // fa-bullseye
            }
          }
        }
      }

      &:last-child {
        a {
          padding-bottom: 0;

          &::after {
            display: none;
          }
        }
      }
    }
  }
}

.mobile-logo {
  float: left;
}

.nav-open {
  overflow: hidden;

  .top-bar {
    background: $white;
    height: 100vh;
  }
}

.title-bar {
  background: $light-gray;
  padding: 0;
}

@media only screen and (min-width: $medium) {
  .top-bar {
    .top-bar-left {
      width: 100%;
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  header {
    a {
      &.mobile-menu-button {
        display: none;
      }
    }

    .menu {
      text-align: right;

      &.dropdown > li {
        &.opens-right > .is-dropdown-submenu {
          right: 0;
        }
      }

      li {
        display: inline-block;
        position: initial;

        a {
          padding: 6px 11px;

          &::before {
            @include pseudo-element;
            bottom: -13px;
            display: none;
            height: 0;
            left: 40%;
            width: 0;
          }
        }

        &:hover,
        &.focus {
          a {
            color: $gray-placeholder-light;
          }
        }

        &:last-child {
          > a {
            padding-right: 0;
          }
        }
      }
    }
  }

  .main-nav {
    padding-top: 4px;

    .menu {
      li {
        a {
          color: $dark-gray;
          position: relative;

          &:hover {
            color: $gray-placeholder-dark;
          }
        }

        .submenu {
          > li {
            &:not(:first-child) {
              > a {
                &::before {
                  top: 6px;
                }

                &::after {
                  top: 27px;
                }
              }
            }
          }
        }
      }
    }

    .submenu {
      @include box-shadow(0 8px 6px -6px transparentize($gradient-gray-dark, .5));
      @include clearfix;
      background: $gray-placeholder-light;

      &.in-focus {
        display: block;
        height: auto;
        max-height: 700px;
        opacity: 1;
        padding: 25px;
      }

      li {
        margin-left: auto;
        margin-right: 0;
        text-align: left;
        width: 40%;

        &:first-child {
          display: inline-block;
          float: left;
          margin-left: 0;
          padding-left: 30px;
          padding-right: 30px;
          width: 60%;

          a {
            color: $white;
            margin-bottom: 1rem;

            &:hover {
              color: $lime-green;
            }
          }

          > p {
            @include body-large;
            @include body-bold;
            color: $black;
            display: inline;
            font-size: 1.2rem;
            letter-spacing: .03em;
          }
        }
      }
    }
  }
}

@media only screen and (min-width: $large) {
  .main-nav {
    padding-top: 13px;
  }
}

@media only screen and (min-width: $xlarge) {
  header {
    .is-dropdown-submenu {
      top: 130%;
    }
  }

  .main-nav {
    padding-top: 10px;

    .submenu {
      li {
        &:first-child {
          p {
            font-size: 2rem;
          }
        }
      }
    }
  }
}
