footer {
  background: $dark-gray;
  font-size: .85em;

  p {
    color: $lightest-gray;
  }

  h4 {
    @include heading-xsmall;
  }
}

.footer-sponsor {
  background: $darkest-gray;
  padding: 30px 0;

  p {
    color: $white;
    margin: 0;
  }

  .logo {
    padding-right: 20px;
    text-align: right;
    width: 15%;
  }

  .description {
    padding-right: 20px;
  }

  .row {
    margin-right: 0;
  }
}

.footer-addresses {
  width: auto;
}

.footer-menu-secondary {
  margin-bottom: 20px;
  margin-top: 1rem;

  li {
    margin-bottom: 5px;

    a {
      color: $lightest-gray;

      i {
        padding-right: 5px;
      }

      &:link,
      &:visited {
        color: $lightest-gray;
      }

      &:hover,
      &:active {
        color: $lime-green;
      }
    }
  }
}

.footer-menu-tertiary {
  margin-top: 20px;

  li {
    a {
      @include upper;
      color: $lightest-gray;

      &:link,
      &:visited {
        color: $lightest-gray;
      }

      &:hover,
      &:active {
        color: $lime-green;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  small,
  ul,
  li {
    color: $lightest-gray;
  }

  small,
  li {
    margin-right: 20px;
  }

  small {
    font-size: 100%;
  }
}

.newsletter-signup {
  width: 80%;

  form {
    position: relative;

    &::after {
      @include pseudo-element;
      border-bottom: 15px solid transparent;
      border-left: 15px solid $gray-placeholder-light;
      border-top: 15px solid transparent;
      height: 0;
      right: -15px;
      top: 0;
      width: 0;
    }
  }

  input {
    &[type='email'] {
      background: $white;
      border: 0;
      height: 30px;
      margin: 0;
      outline: none;
      width: 70%;
    }

    &[type='button'] {
      @include upper;
      @include border-radius(0);
      -moz-appearance: none;
      -webkit-appearance: none;
      background: $gray-placeholder-light;
      border: 0;
      color: $white;
      cursor: pointer;
      font-size: 11px;
      font-weight: bold;
      height: 30px;
      line-height: 2.7;
      outline: 0;
      position: absolute;
      right: 0;
      text-align: center;
      top: 0;
      width: 30%;
    }
  }

  label {
    left: -9999px;
    position: absolute;
    top: -9999px;
  }
}

.footer-address-postal,
.footer-address-shipping {
  display: none;
}

@media only screen and (max-width: $medium) {
  footer {
    .footer-sponsor {
      p {
        margin-bottom: 1rem;
      }

      .table,
      .cell {
        display: block;
        width: 100%;

        &.logo {
          a {
            display: block;
            margin: 0 auto 10px;
            width: 75px;
          }
        }
      }
    }
  }
}

// foundation medium begins
@media only screen and (min-width: $medium) {
  footer,
  .footer-sponsor {
    font-size: .9em;
  }

  .footer-menu-secondary {
    text-align: center;

    li {
      display: inline-block;
      margin-right: 12px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  .footer-addresses,
  .footer-subscribe {
    text-align: center;
  }

  .footer-menu-tertiary {
    small,
    ul,
    li {
      display: inline-block;
    }
  }

  .footer-address-postal,
  .footer-address-shipping {
    display: block;
  }
}

// foundation large begins
@media only screen and (min-width: $large) {
  footer,
  .footer-sponsor {
    font-size: 1rem;
  }

  .footer-menu-secondary {
    text-align: left;

    li {
      display: block;
      margin-right: 0;
    }
  }

  .footer-addresses,
  .footer-subscribe {
    text-align: left;
  }
}
