.with-sidebar {
  .primary-content {
    article,
    .post,
    .featured-image {
      max-width: 980px;
    }

    .row {
      margin-right: 0;
      max-width: 103.5rem;
      width: 98%;
    }

    .sidebar {
      padding-right: 0;
    }
  }
}

@media only screen and (max-width: $medium) {
  .panel {
    &.primary-content {
      .sidebar {
        margin-top: 1rem;
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  .with-banner {
    &.with-sidebar {
      .sidebar {
        margin-top: -80px;
      }
    }
  }
}

@media only screen and (min-width: $xxlarge) {
  .with-sidebar {
    .primary-content {
      .row {
        margin-right: auto;
        max-width: 85rem;
      }
    }
  }
}
