* {
  &::before
  &::after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
}

html,
body {
  color: $dark-gray;
  height: 100%;
}

body {
  -webkit-font-smoothing: initial;
  background-color: $lightest-gray;
}

a {
  color: $gray-placeholder-light;
  text-decoration: none;

  &:link {
    color: $gray-placeholder-light;
  }

  &:visited {
    color: $gradient-gray-dark;
  }

  &:hover,
  &:active {
    color: $gray-placeholder-dark;
  }

  img {
    border: 0;
    outline: none;
  }
}

h3,
h4,
h5,
h6 {
  color: $gray-placeholder-light;
}

ul,
ol {
  margin-bottom: 0;
  margin-left: 2.5rem;
  margin-top: 0;
}

figure,
blockquote {
  margin: 0;
  padding: 0;
}

table {
  border: 0;

  thead,
  tbody {
    background: $white-opaque;
    border: 0;
  }

  thead {
    display: none;
  }

  td {
    border-bottom: 1px solid $white;
    display: block;
    font-size: .8rem;
    text-align: right;

    &:last-child {
      border-bottom: 0;
    }

    &::before {
      @include body-bold;
      content: attr(data-label);
      float: left;
      text-transform: uppercase;
    }
  }

  ul {
    list-style-type: none;

    li {
      list-style-type: none;
      margin-bottom: 10px;
    }
  }
}

cite {
  color: $white;

  &::before {
    display: none;
  }
}

hr {
  background: $dark-gray;
  border: 0;
  color: $dark-gray;
  height: 1px;
  margin: 2rem 0;
}

button,
.button {
  background: $dark-blue;
}

.featured-image {
  border-color: $dark-gray;
  border-style: solid;
  border-width: 10px;
  margin-bottom: 1.5rem;
  position: relative;

  img {
    margin-bottom: 0 !important;
    width: 100%;
  }

  figcaption {
    background: $dark-gray;
    color: $white;
    padding: 10px 10px 1px;
    right: 0;

    p {
      font-size: 13px !important;
      margin: 0;

      a {
        color: $lime-green !important;

        &:link,
        &:visited {
          color: $lime-green !important;
        }

        &:hover,
        &:active {
          color: $lime-green !important;
          text-decoration: underline;
        }
      }
    }
  }
}

article {
  &.content {
    figure {
      border-color: $dark-gray;
      border-style: solid;
      border-width: 10px;
      margin-bottom: 1.5rem;
      position: relative;

      img {
        margin-bottom: 0 !important;
        width: 100%;
      }

      figcaption {
        background: $dark-gray;
        color: $white;
        padding: 10px 10px 1px;
        right: 0;

        p {
          font-size: 13px !important;
          margin: 0;

          a {
            color: $lime-green !important;

            &:link,
            &:visited {
              color: $lime-green !important;
            }

            &:hover,
            &:active {
              color: $lime-green !important;
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}

::selection {
  background: $lime-green;
  color: $dark-gray;
}

.body-content {
  min-height: 75%;
}

.table {
  display: table;

  .cell {
    display: table-cell;

    &.top {
      vertical-align: top;
    }

    &.middle {
      vertical-align: middle;
    }

    &.bottom {
      vertical-align: bottom;
    }
  }
}

.center {
  text-align: center;
}

.panel {
  padding: 40px 0;
}

.row,
.container {
  margin: 0 auto;
  max-width: 72.5rem;
  width: 96%;

  &.narrow {
    @include narrow-max-width;
  }
}

footer,
.banner,
.featured-sections,
.home-happenings,
.home-hero,
.members-details-list,
.offerings,
.programs-listing,
.related-links,
.related-page-section,
.related-posts,
.related-sections,
.sidebar-breadcrumb,
.cta-wrapper,
.banner-news {
  ul,
  ol {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
}

// related to foundation
.columns-centered {
  float: none;
  margin: 0 auto;
}

.column + .column,
.column + .columns,
.columns + .column,
.columns + .columns {
  &:last-child {
    float: left;
  }
}

.bottom-edge {
  background: $darkest-gray;
  content: '';
  display: block;
  font-size: 1px;
  height: 1px;
  line-height: 1px;
}

.content {
  a {
    text-decoration: underline;
  }

  h1,
  h2 {
    @include heading-large;
    color: $dark-gray;
  }

  h3,
  h4 {
    @include heading-small;
  }

  h5,
  h6 {
    @include heading-xsmall;
  }

  img {
    margin-bottom: 1.5rem;

    &.align-left {
      margin-right: 1.5rem;
    }

    &.align-right {
      margin-left: 1.5rem;
    }
  }
}

.primary-content,
.page-content {
  .content {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      @include body-bold;
    }
  }

  &.caption {
    .row {
      @include narrow-max-width;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin-bottom: .25rem;
      margin-top: 0;
    }

    p {
      @include body-large;
      line-height: 1.8;
      margin-bottom: 2.5rem;
      margin-top: 0;

      &.heading-xsmall-normal {
        @include heading-xsmall;
        @include body-semi-bold;
        font-size: 1rem;
        line-height: 1;
      }
    }
  }
}

@media only screen and (min-width: $medium) {
  table {
    border-collapse: collapse;
    margin-bottom: 1.5rem;

    thead,
    tbody {
      tr {
        &:nth-child(even) {
          background-color: $blue-gray;
          border-bottom: solid 1px $gray;
        }
      }
    }

    ul {
      margin-bottom: 0 !important;
      margin-top: 0 !important;
    }

    th,
    td {
      padding: 10px;
      vertical-align: top;
    }

    tr {
      border-bottom: solid 1px $gray-placeholder-dark;

      &:last-child {
        border-bottom: 0;
      }
    }
  }
}

@media only screen and (min-width: $mediumlarge) {
  table {
    thead {
      display: table-header-group;
    }

    th {
      @include heading-xsmall;
      padding: 15px 10px 10px;
      text-align: left;
    }

    td {
      display: table-cell;
      font-size: 1rem;
      text-align: left;

      &::before {
        display: none;
      }
    }

    ul {
      list-style-type: disc;

      li {
        list-style-type: inherit;
        margin-bottom: 0;
      }

      ul {
        ul {
          list-style-type: square;
        }
      }
    }
  }

  .featured-image {
    border-width: 14px;
  }

  article {
    &.content {
      figure {
        border-width: 14px;
      }
    }
  }
}
