.cta-wrapper {
  @include tranzish(all 200ms ease-in-out);
  display: inline-block;
  line-height: 1;
  margin: 0;
  padding: 0;
  position: relative;

  a {
    @include tranzish(all 200ms ease-in-out);
    @include heading-xsmall;
    display: inline-block;
    margin: 0;
    text-decoration: none;
  }

  &::before,
  &::after {
    @include pseudo-element;
    @include tranzish(all 200ms ease-in-out);
    height: 0;
    right: -18px;
    top: 0;
    width: 0;
  }

  &.large {
    border-style: solid;
    border-width: 3px;
    height: 46px;

    a {
      font-size: 19px;
      height: 40px;
      padding-bottom: 3px;
      padding-left: 30px;
      padding-right: 15px;
      padding-top: 11px;
    }

    &::before {
      border-bottom: 23px solid transparent;
      border-left-style: solid;
      border-left-width: 20px;
      border-top: 23px solid transparent;
      height: 0;
      right: -22px;
      top: -3px;
      width: 0;
    }

    &::after {
      border-bottom: 20px solid transparent;
      border-left-style: solid;
      border-left-width: 18px;
      border-top: 20px solid transparent;
      right: -17px;
      top: 0;
    }

    // for the back button, only used with dark-blue-on-gray class
    &.back-button {
      border-right-style: solid;
      border-right-width: 3px;

      a {
        padding-left: 15px;
        padding-right: 30px;
      }

      &::before {
        border-left: 0;
        border-right-style: solid;
        border-right-width: 20px;
        left: -23px;
        right: auto;
      }

      &::after {
        border-left: 0;
        border-right-style: solid;
        border-right-width: 18px;
        left: -18px;
        right: auto;
      }
    }
  }

  &.small {
    border-style: solid;
    border-width: 3px;
    height: 30px;

    a {
      @include body-bold;
      font-size: 11px;
      height: 24px;
      padding: 7px 6px 1px 12px;
    }

    &::before {
      border-bottom: 15px solid transparent;
      border-left-style: solid;
      border-left-width: 15px;
      border-top: 15px solid transparent;
      height: 0;
      right: -17px;
      top: -3px;
      width: 0;
    }

    &::after {
      border-bottom: 12px solid transparent;
      border-left-style: solid;
      border-left-width: 12px;
      border-top: 12px solid transparent;
      right: -12px;
      top: 0;
    }
  }

  &.white-on-color {
    background: $white;
    border-color: $white;

    &::before {
      border-left-color: $white;
    }

    &::after {
      border-left-color: $white;
    }

    a {
      &:link,
      &:visited {
        color: $gray-placeholder-dark;
      }

      &:hover,
      &:active {
        color: $gray-placeholder-dark;
      }
    }
  }

  &.dark-blue-on-gray {
    background: $lightest-gray;
    border-color: $dark-blue;

    &:hover {
      border-color: $gray-placeholder-dark;

      &::before {
        border-left-color: $gray-placeholder-dark;
      }
    }

    a {
      color: $dark-blue;

      &:link,
      &:visited {
        color: $dark-blue;
      }

      &:hover,
      &:active {
        color: $dark-blue;
      }
    }

    &::before {
      border-left-color: $dark-blue;
    }

    &::after {
      border-left-color: $lightest-gray;
    }

    // for the back button
    &.back-button {
      &::before {
        border-right-color: $dark-blue;
      }

      &::after {
        border-right-color: $lightest-gray;
      }

      &:hover {
        &::before {
          border-right-color: $gray-placeholder-dark;
        }
      }
    }
  }

  &.lime-green-on-gray {
    background: $gradient-gray-light;
    border-color: $white;

    &:hover {
      border-color: $lime-green;

      &::before {
        border-left-color: $lime-green;
      }
    }

    a {
      color: $white !important;

      &:link,
      &:visited {
        color: $white !important;
      }

      &:hover,
      &:active {
        color: $white !important;
      }
    }

    &::before {
      border-left-color: $white;
    }

    &::after {
      border-left-color: $gradient-gray-light;
    }
  }
}

@media only screen and (max-width: $medium) {
  .cta-wrapper {
    &.large {
      height: 30px;

      a {
        font-size: 11px;
        height: 24px;
        padding: 6px 6px 1px 12px;
      }

      &::before {
        border-bottom: 15px solid transparent;
        border-left-style: solid;
        border-left-width: 15px;
        border-top: 15px solid transparent;
        right: -17px;
        top: -3px;
      }

      &::after {
        border-bottom: 12px solid transparent;
        border-left-style: solid;
        border-left-width: 12px;
        border-top: 12px solid transparent;
        right: -12px;
        top: 0;
      }
    }
  }
}
